import React, { createContext, useContext, useState, useEffect, ReactNode } from 'react';
import { useAuth } from './auth-context';

// Extender el tipo User para incluir el ELO
interface ExtendedUser {
  id: string;
  username: string;
  email: string;
  fullName?: string;
  avatar?: string;
  role: 'STUDENT' | 'TEACHER' | 'ADMIN';
  elo?: number; // Añadimos el ELO como propiedad opcional
}

// Tipos para los duelos
export interface Duelo {
  id: number;
  duelId?: string;
  opponent: {
    name: string;
    avatar: string;
    level: number;
  };
  status: 'disponible' | 'en_curso' | 'completado';
  topic: string;
  result?: 'victoria' | 'derrota' | 'empate';
  rewards?: {
    xp: number;
    coins: number;
  };
  timeLeft?: string;
}

// Tipos para las notificaciones de duelos
export interface DuelNotification {
  duelId: string;
  challenger: {
    id: string;
    name: string;
    avatar: string;
    level: number;
    elo: number;
  };
  topic: string;
  timestamp: number;
}

interface DuelContextType {
  duelos: Duelo[];
  pendingDuels: DuelNotification[];
  showNotification: boolean;
  currentNotification: DuelNotification | null;
  wsConnection: WebSocket | null;
  acceptDuel: (duelId: string) => Promise<void>;
  rejectDuel: (duelId: string) => Promise<void>;
  closeNotification: () => void;
  connectToWebSocket: () => void;
  disconnectWebSocket: () => void;
}

const DuelContext = createContext<DuelContextType | undefined>(undefined);

export const DuelProvider: React.FC<{ children: ReactNode }> = ({ children }) => {
  const { user } = useAuth();
  const [duelos, setDuelos] = useState<Duelo[]>([]);
  const [pendingDuels, setPendingDuels] = useState<DuelNotification[]>([]);
  const [showNotification, setShowNotification] = useState(false);
  const [currentNotification, setCurrentNotification] = useState<DuelNotification | null>(null);
  const [wsConnection, setWsConnection] = useState<WebSocket | null>(null);

  // Conectar al WebSocket cuando el usuario inicia sesión
  useEffect(() => {
    if (user?.id) {
      connectToWebSocket();
    }

    return () => {
      disconnectWebSocket();
    };
  }, [user?.id]);

  // Mostrar la siguiente notificación cuando se cierra la actual
  useEffect(() => {
    if (!showNotification && pendingDuels.length > 0) {
      const nextNotification = pendingDuels[0];
      setCurrentNotification(nextNotification);
      setShowNotification(true);
      // Eliminar la notificación de la cola
      setPendingDuels(prev => prev.slice(1));
    }
  }, [showNotification, pendingDuels]);

  const connectToWebSocket = () => {
    if (!user?.id || wsConnection) return;

    // Crear nueva conexión WebSocket para notificaciones
    const ws = new WebSocket(`ws://localhost:8002/ws/notifications/${user.id}`);

    ws.onopen = () => {
      console.log('Conexión WebSocket establecida para notificaciones de duelos');
      // Enviar el ELO actual del jugador al conectarse
      if (ws.readyState === WebSocket.OPEN) {
        // Usar el usuario como ExtendedUser para acceder a la propiedad elo
        const extendedUser = user as unknown as ExtendedUser;
        ws.send(JSON.stringify({ 
          type: 'connect',
          elo: extendedUser.elo || 0 
        }));
      }
    };

    ws.onmessage = (event) => {
      try {
        const data = JSON.parse(event.data);
        
        // Si es una notificación de duelo
        if (data.type === 'duel_request') {
          const newNotification: DuelNotification = {
            duelId: data.duelId,
            challenger: {
              id: data.challenger.id,
              name: data.challenger.name,
              avatar: data.challenger.avatar || 'https://randomuser.me/api/portraits/lego/1.jpg',
              level: data.challenger.level || 1,
              elo: data.challenger.elo || 0
            },
            topic: data.topic,
            timestamp: Date.now()
          };

          // Si no hay notificación activa, mostrar esta
          if (!showNotification) {
            setCurrentNotification(newNotification);
            setShowNotification(true);
          } else {
            // Si ya hay una notificación activa, añadir a la cola
            setPendingDuels(prev => [...prev, newNotification]);
          }
        }
        
        // Si es una actualización de duelos
        if (data.type === 'duels_update') {
          setDuelos(data.duels);
        }
      } catch (error) {
        console.error('Error al procesar mensaje WebSocket:', error);
      }
    };

    ws.onerror = (error) => {
      console.error('Error en la conexión WebSocket:', error);
    };

    ws.onclose = () => {
      console.log('Conexión WebSocket cerrada');
      // Intentar reconectar después de un tiempo
      setTimeout(() => {
        if (user?.id) connectToWebSocket();
      }, 5000);
    };

    setWsConnection(ws);
  };

  const disconnectWebSocket = () => {
    if (wsConnection) {
      wsConnection.close();
      setWsConnection(null);
    }
  };

  const acceptDuel = async (duelId: string) => {
    try {
      // Llamar directamente al servicio de duelos para aceptar el duelo
      const response = await fetch(`http://localhost:8002/api/duels/accept`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ duel_id: duelId }),
      });

      if (!response.ok) {
        throw new Error('Error al aceptar el duelo');
      }

      // Notificar al servidor a través del WebSocket
      if (wsConnection && wsConnection.readyState === WebSocket.OPEN) {
        wsConnection.send(JSON.stringify({
          type: 'duel_accepted',
          duelId
        }));
      }

      // Conectar al duelo aceptado
      if (user?.id) {
        // Abrir una nueva pestaña con la URL del duelo
        window.open(`/duelos?duelId=${duelId}&playerId=${user.id}`, '_blank');
      }

      // Cerrar la notificación
      closeNotification();
    } catch (error) {
      console.error('Error al aceptar duelo:', error);
    }
  };

  const rejectDuel = async (duelId: string) => {
    try {
      // Llamar directamente al servicio de duelos para rechazar el duelo
      const response = await fetch(`http://localhost:8002/api/duels/reject`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ duel_id: duelId }),
      });

      if (!response.ok) {
        throw new Error('Error al rechazar el duelo');
      }

      // Notificar al servidor a través del WebSocket
      if (wsConnection && wsConnection.readyState === WebSocket.OPEN) {
        wsConnection.send(JSON.stringify({
          type: 'duel_rejected',
          duelId
        }));
      }

      // Cerrar la notificación
      closeNotification();
    } catch (error) {
      console.error('Error al rechazar duelo:', error);
    }
  };

  const closeNotification = () => {
    setShowNotification(false);
    setCurrentNotification(null);
  };

  return (
    <DuelContext.Provider
      value={{
        duelos,
        pendingDuels,
        showNotification,
        currentNotification,
        wsConnection,
        acceptDuel,
        rejectDuel,
        closeNotification,
        connectToWebSocket,
        disconnectWebSocket
      }}
    >
      {children}
    </DuelContext.Provider>
  );
};

export const useDuel = () => {
  const context = useContext(DuelContext);
  if (context === undefined) {
    throw new Error('useDuel debe ser usado dentro de un DuelProvider');
  }
  return context;
};
